.mobile__cart__summary {
  text-align: center;
  position: relative;
}
.mobile__cart__summary__info {
  position: relative;
  display: inline-block;
  /* max-width: 20rem; */
  margin: 0.5rem auto 0 auto;
  padding: 1.1rem 0 0 4rem;
  text-align: left;
}
.mobile__cart__summary__cart__with__price {
  white-space: nowrap;
}
.mobile__cart__summary .flaticon-cart::before {
  margin: 0;
  color: #5a5a5a;
  font-size: 3.2rem;
  position: absolute;
  top: -1rem;
  left: 0rem;
}
.mobile__cart__summary__count {
  border-radius: 3rem;
  border: 1px solid white;
  color: white;
  text-align: center;
  width: 2rem;
  height: 2rem;
  padding: 0.2rem 0 0 0;
  position: absolute;
  top: 1.2rem;
  left: 1.3rem;
}
.mobile__cart__summary__price {
  display: inline-block;
  margin-left: 0.5rem;
}

.mobile__cart__summary__toggle {
  width: 3rem;
  height: 3rem;
  background: white;
  border-radius: 2rem;
  position: absolute;
  top: -1.2rem;
  left: 46%;
  color: #5a5a5a;
}
.mobile__cart__summary__toggle .flaticon-chevron-up::before {
  font-size: 1.2rem;
  margin: 0;
  top: initial;
  left: 0.9rem;
}
.mobile__sliding__cart__open
  .mobile__cart__summary__toggle
  .flaticon-chevron-up::before {
  transform: rotate(-180deg);
  transition: transform 400ms ease;
  display: block;
}
