.mobile__cart__view .mobile__cart__summary {
  background-color: white;
  height: 4rem;
}
.mobile__cart__view .cart__items__container {
  position: relative;
  max-height: 100%;
}
.mobile__cart__view .cart__items__container .cart__items__container__content {
  position: fixed;
  overflow-y: auto;
  max-height: 100%;
  width: 100%;
  bottom: 4rem;
  top: 6.8rem;
}
.mobile__cart__view .cart__items__container .cart__item,
.mobile__cart__view .cart__items__container .cart__item__summary {
  margin: 0.5rem 0.5rem 0.5rem 0.5rem;
  width: initial;
  border-radius: 0.5rem;
  border: 1px solid #d5d5d5;
}
.mobile__cart__view .cart__items__container .cart__item__summary {
  margin: 0.5rem;
}
.mobile__cart__view
  .cart__items__container
  .cart__item
  .cart__item__button__edit {
  display: none;
}
.mobile__cart__view .cart__item__button__remove {
  position: absolute;
  right: 0.3rem;
}
.mobile__cart__view .cart__column__cart__empty__label {
  display: none;
}
